// axios
import reqaxios from "../axios";
// 
export const getAddress = (userid) => reqaxios({url:`/v1/users/${userid}/addresses`,method:'get'});
// 
export const addAddress = (params,userid) => reqaxios.post(`v1/users/${userid}/addresses`,params);
// 
export const deleteAddress = (userid,addressid) => reqaxios({url:`/v1/users/${userid}/addresses/${addressid}`,method:'delete'});
