// api
import food from '@/api'
// import axios from 'axios'
const {getFoodData,getrestaurantsList,getRestaurantsAttrList,getDeliveryModes,getCategoryList} = food
const state = {
    foodData:[], //
    restaurantsList:[],//
    restaurantsAttrList :[],//s
    deliveryModesList :[],//
    categoryList:[],//
}
const mutations = {
    GETFOODDATA(state,data){
        state.foodData = data
        // console.log(state.foodData,data)
    },
    GETRESTAURANTSLIST(state,data){
        state.restaurantsList = data
    },
    GETRESTAURANTSATTRLIST(state,data){
        state.restaurantsAttrList =data
    },
    GETDELIVERYMODES(state,data){
        state.deliveryModesList = data
    },
    GETCATEGORYLIST(state,data){
        state.categoryList = data
    }
 
}
const actions = {
    // 
    async getFoodData ({commit}){
 
        let result = await getFoodData()
        // let result = await axios({
        //     url:'https://elm.cangdu.org/shopping/v2/restaurant/category'
        // })
        // console.log('',result)
            commit ('GETFOODDATA',result)
        
        
    },
    //
    async getrestaurantsList({commit},classifyItem){
  
        let result = await getrestaurantsList(classifyItem)      
        commit ('GETRESTAURANTSLIST',result)
    },
    // 
    async getRestaurantsAttrList ({commit}){
        let result  =await getRestaurantsAttrList()
        // console.log('',result)
        result.map(item=>item.isChoose=false)
        commit('GETRESTAURANTSATTRLIST',result)
    },
    // 
    async getDeliveryModes({commit}){
        let result = await getDeliveryModes()
        // console.log(result,'')
        commit('GETDELIVERYMODES',result)
    },
    //
    async getCategoryList({commit}){
        let result = await getCategoryList()
        
        commit('GETCATEGORYLIST',result)
    }
    ,

}
const getters = {
    
   
}
export default {
    state,mutations,actions,getters
}