<template>
  <div class="page">
    <!--  -->
    <Header title="" />
    <!--  -->
    <form class="loginAndRegister">
      <input class="account" type="text" placeholder="" v-model="phone" />
      <input
        class="password"
        type="text"
        placeholder=""
        v-model="password"
      />
      <div class="VerificationCode">
        <input
          class="verification"
          type="text"
          placeholder=""
          v-model="codeNumber"
        />
       
         <div class="photo"><img :src="code" alt="" /></div>
        <!-- <div class="photo"><img :src="code" alt="" /></div> -->
        <span class="send" @click="getVcode()"></span>
      </div>
    </form>
    <!--  -->
    <p></p>
    <p></p>
    <!--  -->
    <div class="button">
      <button @click="loginMyself"></button>
    </div>
    <!--  -->
    <a class="reset">?</a>
  </div>
</template>

<script>
// 
import request from "../../api/index";
import { mapState } from "vuex";
import Header from "../../components/Header";
// import axios from "axios";
export default {
  name: "loginAndRegister",
  data() {
    return {
      phone: "18683257353", //
      password: "111111", //
      codeNumber: null, //
      userInfo: {}, //
      // code1:''
    };
  },
  components: {
    Header
  },
  computed: {
    ...mapState({
      code: (state) => state.login.code,
    }),
  },
  mounted() {
    this.getVcode();
  },
  methods: {
    async getVcode() {
     this.$store.dispatch("getVcode");
    //  const result = await axios.post('http://localhost:8001/v1/captchas')
     // console.log(result)
    //  this.code1 = result.data.code

    },
    // 
    async loginMyself() {
      const { phone, password, codeNumber } = this;
      // 
      let num = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
      if (!num.test(phone)) {
        alert("");
        return;
      }
      // 
      if (!password) {
        alert("");
        return;
      }
      // 4
      let codeNum = /^\d{4}$/;
      if (!codeNum.test(codeNumber)) {
        alert("");
        return;
      }
      // 
      // 
      const params = { username: phone, password, captcha_code: codeNumber };

      try {
        let result = await request.userImagess(params);
        // let result = await axios({url:'http://localhost:8001/v2/login',method:'post',params});
        if (result.username) {
          localStorage.setItem("userInfo", JSON.stringify(result));
        }
      } catch (error) {
        console.error(error);
      }
      // this.$store.dispatch("userLogin", params);
      // 
      const userInfo = JSON.parse(localStorage.getItem("userInfo"));
      if (userInfo) {
        this.$router.push("/myself");
      }
      console.log(userInfo);
    },
  },
};
</script>

<style lang="less" scoped>
.page {
  padding: 1.95rem 0;
  position: relative;
  background-color: #f5f5f5;
  width: 100vw;
  height: calc(100vh-1.95rem * 2);
  .loginAndRegister {
    width: 100%;
    margin-top: 1rem;
    input {
      font-size: 0.7rem;
      color: #777;
      height: 1.8rem;
      text-indent: 1rem;
      width: 100%;
      border: none;
      outline: none;
      border-bottom: 1px solid #f1f1f1;
    }
    .VerificationCode {
      background-color: #fff;
      font-size: 0.6rem;
      height: 2rem;
      .verification {
        width: 60%;
      }
      .photo {
        display: inline;
        margin-top: -1rem;
      }
      .send {
        margin-left: 0.5rem;
        color: #3b95e9;
      }
    }
  }
  p {
    font-size: 0.5rem;
    color: red;
    padding: 0.4rem 0.6rem;
    line-height: 0.5rem;
  }
  .button {
    display: flex;
    justify-content: center;
    button {
      width: 95%;
      font-size: 0.7rem;
      color: #fff;
      background-color: #4cd964;
      padding: 0.5rem 0;
      border: none;
      border-radius: 0.15rem;
      text-align: center;
    }
  }
  .reset {
    position: absolute;
    right: 0.5rem;
    margin-top: 1rem;
    color: #3b95e9;
    font-size: 0.6rem;
  }
}
</style>
